.form {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.input {
    padding: 8px;
    font-size: 16px;
    border: 2px solid #ccc;
    border-radius: 4px;
    margin-right: 8px;
}
.input:focus {
    border-bottom: 2px solid #0055CC;
}

.formBtn {
    padding: 8px 16px;
    background-color: #0077FF;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.formBtn:hover {
    background-color: #0055CC;
}

.list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.item {
    margin: 16px 0;
    font-size: 18px;
    display: flex;
    align-items: center;
}

.item::before {
    content: "";
    display: inline-block;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: #0077FF;
    margin-right: 8px;
}

.item a {
    color: #333;
    text-decoration: none;
    transition: color 0.3s ease;
}

.item a:hover {
    color: #0077FF;
}